html, body {
  width: 100%;
  height: 100%;
}

body {
  overflow: auto;
  padding: 5px;
  font: 14px/18px "Helvetica Neue", Arial,Helvetica,sans-serif;
  color: #393939;
}

.input-new {
  border: 0;
  height: 25px;
  margin-top: 5px;
  outline: none;
  padding: 5px 0px 5px 5px;
  background-color: #E3E3E3;
}

.input-new:hover {
  background-color: #CECECE;
  cursor: pointer;
}

.input-new:focus {
  border: 0;
}

.lists {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}

.list {
  background-color: #E3E3E3;
  margin: 5px 5px;
  padding: 10px;
  width: 260px;
}

.list > header {
  font-weight: bold;
  font-size: 15px;
  line-height: 18px;
}

.list article {
  border-radius: 3px;
  margin-top: 5px;
}

.list .card {
  background-color: #FFF;
  border-bottom: 1px solid #CCC;
  padding: 6px 8px;
  cursor: -webkit-grab;
  cursor: grab;
}

.list .card:hover {
  background-color: #F0F0F0;
}

[ng-drag] {
  -moz-user-select: -moz-none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

[ng-drag] {
  cursor: move;
}

[ng-drag].dragging {
  opacity: 0.5;
}

[ng-drop].drag-enter {
  opacity: 0.5;
}

[ng-drop] span.title {
  display: block;
  position: absolute;
}

[ng-drop] div {
  position: relative;
  z-index: 2;
}

